.sidebar {
  position: fixed;
  z-index: $z-index-sidebar;
  top: 0;
  left: 0;
  flex: 1 0 auto;
  height: 100%;
  display: flex;
  color: $color-white;

  transition: left 300ms ease-in-out;

  @media screen and(max-width: 1024px) {
    left: -$sidebar-width;
    transition: left 300ms ease-in-out;
  }

  &--open {
    left: 0;
    transition: left 300ms ease-in-out;
  }
}

.sidebar__content {
  position: relative;
  width: $sidebar-width;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #070d59;
}

.sidebar__logo {
  margin-top: rem(70);
  margin-left: rem(50);

  & > img {
    width: auto;
  }
}

.sidebar__list {
  flex: 1;
  margin-top: rem(50);
  padding-left: rem(20);
  padding-right: rem(20);
  min-height: 0;
  overflow-y: auto;
  padding-bottom: 100px;
}

.sidebar__list-label {
  margin-left: rem(20);
  margin-bottom: rem(10);
  color: rgba(255, 255, 255, 0.27);
  font-size: rem(18);
  line-height: rem(31);
}

.sidebar__files {
  padding-left: 0;

  & > li:not(:first-child) {
    margin-top: rem(10);
  }
}

.sidebar__upload {
  width: 100%;
  padding: rem(40);
  box-sizing: border-box;
}

.sidebar__toggle {
  padding: rem(10);
  color: red;
  position: fixed;
  top: 0;
  left: 0;
  transition: left 300ms ease-in-out;
  background-color: $color-bg-sidebar;
  border: 0;
  outline: none;
  cursor: pointer;

  &--open {
    left: $sidebar-width;
    transition: left 300ms ease-in-out;
  }

  @media screen and (min-width: 1025px) {
    display: none;
  }
}
